

/*--BLOG-SECTION--*/

.foodko-blog-wrap{
    transition: all .4s;
    @include max (1199px){
        // max-width: 600px;
        // margin: 0 auto;
        padding-top: 24px;
          }
         
          &.one{
            background: var(--white-color);
            padding: 30px;
            border-radius: 10px;
            
            @include max (1199px){
              margin-top: 24px;
             }
            a{
              
            }
            .foodko-blog-content{
              border: 0px;
              padding: 0;
              // @include max (767px){
              //   text-align: center;
              //          }
              a{
                h4{
                  margin: 20px 0px 10px;
                  font-family: $heading-font;
                }
              }
              p{

              }
            }
          }
          &.blog-gird-inner{
            background: var(--background-color);
            margin-top: 24px;

          }
          &.blog-inner{
            background: var(--background-color);
            margin-top: 24px;
           
            &:first-child{
              margin-top: 0px;
            }
            .foodko-blog-meta {
              padding-top: 24px;
          }
          h4{
            margin: 15px 0 10px !important;
          }
          .foodko-blog-btn {
            margin-top: 25px !important;
        }
          }
          &.inner-blog-detalis{
            padding: 0px;
            .foodko-blog-content{
              padding: 40px 0px 0px 0px;
              border: none;
              h4{
                margin: 15px 0px 18px;
              }
              p{
                font-size: 21px;
                padding-bottom: 40px;
              }
        
            }
          }
    a{
      
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
        
    }
    .foodko-blog-content {
        padding: 30px;
        border-radius: 0 0 10px 10px;
      border: 1px solid rgba(57, 61, 61, 0.2);
      border-top: none;
    
      @include max (767px){
       padding: 18px;
          }
        .foodko-blog-meta{
            ul{
                line-height: 1;
                display: flex;
              
                li{
                    position: relative;
                    padding-right: 30px;
                    gap: 5px;
                    display: flex;
                    align-items: center;
                  
                    a{
                        font-size: 15px;
                        color: var(--heading-color);
                    }
                }
            }
        }
        a{
          
        h4{
            margin: 24px 0px 10px;
            }
        }
        p{
            font-size: 18px;
            margin: 0;
        }
        a{
            &.foodko-blog-btn {
                font-weight: 500;
                display: flex;
                align-items: center;
                font-size: 18px;
                font-weight: 700;
                transition: all .4s;
                color: var(--heading-color);
                margin-top: 24px;
                gap: 8px;
                @include max (767px){
                  margin-top: 15px;
                     }
                path {
                  fill: var(--heading-color);
                  transition: fill .4s; 
                }
              
                &:hover {
                  color: var(--accent-color);
                  font-weight: 700; 
                  path {
                    fill: var(--accent-color); 
                  }
                }
             
              }
        }
    }
}
/*--BLOG-SECTION-- END*/

/*--BLOG-SECTION-NAVIGATION START-*/
.foodko-navigation{
  margin-top: 85px;
  @include max (767px){
    margin-top: 30px;
 }
  text-align: center;
  .navigation{
    &.pagination{
      display: flex;
      padding-left: 0;
      list-style: none;
    }
    &.center{
      justify-content: center;
    }
  }
  .nav-links{
    a{
      color: var(--accent-color);
      font-weight: 700 !important;
      &.next{
        padding-right: 40px;
        @include max (767px){
          padding-right: 5px;
       }
      }
      &.page-numbers{
        width: 40px;
        font-weight: 500;
        display: inline-flex;
        text-align: center;
        line-height: 60px;
        font-size: 18px;
        align-items: center;
        justify-content: center;
        height: 40px;
        border: 1px solid var(--accent-color);
        margin-left: 20px;
        border-radius: 3px;
        &:hover{
          background-color: var(--accent-color);
          color: var(--white-color);
          font-weight: 700 !important;
        }
        @include max (767px){
          margin-left: 10px;
       }
      }
    }
    span{
      height: 40px;
      width: 40px;
      font-weight: 700;
      display: inline-flex;
      text-align: center;
      line-height: 60px;
      font-size: 18px;
      align-items: center;
      justify-content: center;
      color: var(--body-color);
      border-radius: 3px;
      background-color: var(--white-color);
      &.page-numbers{

      }
      &.current{
        cursor: not-allowed;
        background-color: var(--accent-color);
        color: var(--white-color);
      }
    }
    a{
    
      &.next1{
        padding-left: 40px;
        @include max (767px){
          padding-left: 5px;
       }
      }
      &.page-numbers{

      }
    }
  }
}

/*--BLOG-SECTION-NAVIGATION END-*/

/*--BLOG-INNER-SECTION START-*/
.foodko-blog-sidebar{
  @include max (991px){
    padding-top: 65px;
    }
  .foodko-blog-widgets{
    border: 1px solid rgba(57, 61, 61, 0.2);
    padding: 30px;
    margin-bottom: 24px;
    border-radius: 10px;
    @include max (1399px){
      padding: 20px;
      }
      @include max (991px){
        padding: 15px;
        }
    .foodko-search-box{
      position: relative;
   
  input[type="search"] {
    width: 100%;
    font-size: 20px;
    padding-right: 50px;
    padding: 18px 80px 18px 17px;
    padding-left: 15px;
    height: 60px;
    border: 1px solid var(--background-color) !important;
    background-color: var(--background-color);

      }
      #foodko-search-btn{
        position: absolute;
        /* left: 0; */
        top: 15px;
        right: 48px;
      }
    }
  }
}


.foodko-blog-widgets{
  h3{
    margin-bottom: 20px;
    font-family: $heading-font;
    font-weight: 700;
  }
a{
  &.foodko-recent-post-item{
    gap: 15px;
    display: flex;
    align-items: center;
    overflow: hidden;
    margin-bottom: 24px;
    @include max (1399px){
      gap: 10px;
      }
      @include max (991px){
        margin-bottom: 20px;
        gap: 15px;
        }
        @include max (570px){
          display: block;
          }
  }

  .foodko-recent-post-thumb{
    display: flex;
    flex-shrink: 0;
    @include max (570px){
      display: inline-flex;
      }
  }
  .foodko-recent-post-data{

    ul{
      display: flex;
   
      li{
        display: flex;
        gap: 5px;
        color: var(--body-color);
        font-size: 12px;
       font-weight: 400;
        align-items: center;
        padding-right: 14px;
        @include max (1399px){
          padding-right: 0px;
          }
          @include max (991px){
            padding-right: 14px;
            }
        img{
          width: 18px;
          height: 18px;
        }
      }
    }
    h4{
      font-family: $heading-font;
      margin-top: 8px;
    }

  }
}
}

.foodko-blog-categorie{
  ul{

    li{
    padding-bottom: 12px;
    &:last-of-type{
      padding-bottom: 0;
    }
      a{
        display: flex;
        font-size: 20px;
        transition: all .4s;
        justify-content: space-between;
        color: var(--heading-color);
     
        span{
          display: flex;
          flex-shrink: 0;
          align-items: center;
          gap: 8px;
          font-size: 21px;
          img{

          }
        }
        p{
          margin-bottom: 0;

        }

      }
    }
  }
}
.foodko-blog-tags{
  ul{
    display: flex;
    justify-content: space-between;
    padding-bottom: 13px;
    &:last-child{
      padding-bottom: 0;
    }
    li{
   
      display: inline-block;
      a{
        padding: 10px 18px;
        font-size: 18px;
        border-radius: 3px;
        display: block;
        transition: all .4s;
        color: var(--heading-color);
        background-color: var(--background-color);
      }
    }
  }
}
/*--BLOG-INNER-SECTION END-*/

/*--BLOG-DETAILS-INNER-SECTION START-*/
.foodko-blog-details-content{
h4{
font-family: $heading-font;
}
p{
  margin: 12px 0px 20px;
}
}
.foodko-blog-details-text{
  margin-bottom: 40px;
  h3{
    margin-bottom: 24px;
    font-family: $heading-font;
  }
  ul{
    li{
      margin-bottom: 12px;
      &:last-child{
        margin-bottom: 0;
      }
      img{
        width: 10px;
        height: 10px;
      }
      span{
        color: var(  --dark-bg);
        font-weight: 700;
        font-size: 20px;
        font-family: $heading-font;
        padding-left: 10px;
      }
    }
  }
}

.foodko-blog-tag-wrapper{
  display: flex;
align-items: center;
  padding: 24px 0px;
  justify-content: space-between;
  border-top: 1px solid rgba(57, 61, 61, 0.2);
  border-bottom: 1px solid rgba(57, 61, 61, 0.2);
  @include max (1199px){
	display: block;
	}
  .foodko-blog-tag-wrap{
    display: flex;
    align-items: center;
    gap: 24px;
	@include max (1199px){
		gap: 10px;
		padding-right: 18px;
		}
		@include max (767px){
		padding-bottom: 20px;
			}
    h3{
      font-family: $heading-font;
    }
    ul{
      display: flex;
      gap: 24px;
	  @include max (1199px){
		gap: 10px;
		}
      li{
        background: rgba(57, 61, 61, 0.15);
        padding: 10px 22px;
        border-radius: 3px;
       
        a{
          color: var(--body-color);
        }
      }
    }
  }
}


.foodko-post-comment{
  padding: 40px 0px;
  h3{
    margin-bottom: 30px;
    font-family: $heading-font;
  }
  ul{
    li{
      margin-bottom: 30px;
      .foodko-post-comment-wrap{
        display: flex;
        gap: 20px;
        .foodko-post-comment-thumb{
          width: 80px;
          height: 80px;
          flex-shrink: 0;
          overflow: hidden;
          border-radius: 100%;
          img{
            width: 100%;
            height: 100%;
            object-fit: cover;
          }
        }
        .foodko-post-comment-data{
          position: relative;

          a{
            font-weight: 700 !important;
            &.foodko-comment-reply{
              position: absolute;
              top: 0;
              right: 0;
              font-size: 20px;
              font-weight: 600;
              transition: all .4s;
              color: var(--body-color);
              display: flex;
              align-items: center;
            }
          }
          strong{
            display: block;
            font-size: 20px;
            line-height: 1;
            margin-bottom: 5px;
            font-family: "Playfair Display", serif;
            font-weight: 700 !important;
            color: #0A0F0F;
            line-height: 28px;
          }
          span{
            font-weight: 400;
            font-size: 12px;
            display: block;
          }
          p{

          }
        }
      }
    }
    li{
      &.children{
        padding-left: 95px;
		@include max (767px){
			padding-left: 0px;
			}
        .foodko-post-comment-wrap{
          &.children2{
            border-top: 1px solid  rgba(57, 61, 61, 0.2);
            padding-top: 30px;
            border-bottom: 1px solid rgba(57, 61, 61, 0.2);
            padding-bottom: 30px;
          }
          .foodko-post-comment-thumb{

          }
          .foodko-post-comment-data{
            strong{

            }
            span{
  
            }
            p{
  
            }
          }
        }
      }
    }
  }
}

.foodko-comment-box{
	h3{
		margin-bottom: 25px;
		font-family: $heading-font;
	}
	.foodko-comment-box-form{
		border: 1px solid rgba(57, 61, 61, 0.2);
		padding: 30px;
		background: var(--background-color);
		border-radius: 10px;
		.foodko-comment-form{
			// margin-bottom: 24px;
			border-bottom: 1px solid #e7e7e8 !important;
			padding-bottom: 50px;
			input{
				margin: 0;
				width: 100%;
				height: 52px;
				font-weight: 700;
				color: var(--heading-color);
				font-size: 18px;
				border-radius: 0;
				background-color: transparent;
				// padding: 15px 0 !important;
				border: none !important;
				transition: all .4s;
		
				&::placeholder {
					color: var(--heading-color); 
				}
			}
			textarea{
				height: 130px;
				resize: none;
				margin: 0;
				width: 100%;
				font-weight: 700;
				color: var(--heading-color);
				font-size: 18px;
				border-radius: 0;
				background-color: transparent;
				padding: 24px 0 0px !important;
				border: none !important;
				transition: all .4s;
				&::placeholder {
					color: var(--heading-color); 
				}
			}
		}
	}
}
/*--BLOG-DETAILS-INNER-SECTION END-*/