 
// footer one
 
 .foodko-footer-center{
    padding-bottom: 70px;
.foodko-footer-wrap{
    p{
        margin:30px 0px 40px;
    font-size: 20px;
    @include max(767px) {
        margin:20px 0px 30px;
      }
    }
    .foodko-social-icon{
      
    }
    @include max(1199px) {
        padding-bottom: 30px;
      }
   
}
.foodko-footer-menu{

     @include max(767px) {
      &:last-of-type{
        padding-top: 20px;
      }
      }
    h4{
      font-family: $heading-font;
    }
    ul{
        padding-top: 30px;
        @include max(767px) {
            padding-top: 15px;
          }
       
        li{
            padding-top: 13px;
            &:first-of-type{
                padding-top: 0px;
            }
        &.text-red{
            a{
                color: red;
            }
        }
            a{
                font-size: 21px;
                color: var(--body-color);
                &:hover{
                    color: var(--background-color2);
             
                }
            }
            &.foodko-time{
                display: flex;
                justify-content: space-between;
                &.one{
                    border-bottom: 1px solid var(--light-bg);
                    &:nth-child(3){
                        border-bottom: none;
                    }
                }

                span{
                    
                }
                p{
                    font-weight: 700;
                    &.font-weight{
                        font-weight: 400;
                    }
                }
            }
        }
    }
}
}
.foodko-footer-bottom.center {
    text-align: center;
    padding: 35px 0px 0px;
    border-top: 1px solid var(--light-bg);
}






