
.foodko-portfolio-box{
    display: block;
 &.gallery{
  padding-bottom: 24px;
 }
        @include max (1199px){
            display: ruby-text;
            padding-top: 24px;
              }
              @include max (767px){
                display: ruby-text;
                padding-top: 0px;
                  }
    .foodko-portfolio-content {
        position: relative;
        width: 100%;
        max-width: 400px;
        margin: auto;
        overflow: hidden;
        margin-bottom: 24px;
        @include max (1199px){
         margin-bottom: 0;
              }
        &:last-of-type{
            margin-bottom: 0px;
        }
      
        .foodko-portfolio-content-overlay {
          background: rgba(0, 0, 0, 0.6);
          position: absolute;
          height: 100%;
          width: 100%;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          opacity: 0;
          transition: all 0.4s ease-in-out 0s;
          border-radius: 10px;
        }
      
        &:hover .foodko-portfolio-content-overlay {
          opacity: 1;
        }
      
        .content-image {
          width: 100%;
        }
      
        .foodko-portfolio-content-details {
          position: absolute;
          text-align: center;
          padding-left: 1em;
          padding-right: 1em;
          width: 100%;
          top: 50%;
          left: 50%;
          opacity: 0;
          transform: translate(-50%, -50%);
          transition: all 0.3s ease-in-out 0s;
        }
      
        &:hover .foodko-portfolio-content-details {
          top: 50%;
          left: 50%;
          opacity: 1;
        }
      
        .foodko-portfolio-content-details h3 {
          color: var(--white-color);
          font-weight: 700;
          margin-bottom: 8px;
        }
      
        .foodko-portfolio-content-details p {
          font-size: 18px;
          font-weight: 700;
        }
      
        .text-star {
          display: flex;
          align-items: center;
          text-align: center;
          justify-content: center;
          gap: 5px;
      
          p {
            margin: 0;
            color: var(--white-color);
          }
      
          img {
            flex-shrink: 0;
          }
        }
      
        .fadeIn-bottom {
          top: 60%;
        }
      }
}








/*--PORTFOLIO-SECTION ISOTOPE START--*/
.portfolio-menu{
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  margin-top: 50px;
  @include max (991px){
    display: ruby-text;
     }
  li{
    border: 1px solid rgba(57, 61, 61, 0.2);
    margin: 0px 10px;
    padding: 14px 15px;
    text-transform: capitalize;
    cursor: pointer;
    display: flex;
    font-weight: 700;
    align-items: center;
    @include max (991px){
      &:last-of-type{
        margin-top: 24px;
      }
       }
       @include max (767px){
        &:nth-of-type(03){
          margin-top: 24px;
        }
         }
    &.active{
      background-color: var(--accent-color);
      color: #fff;
    }
  }
}



/*--PORTFOLIO-SECTION ISOTOPE END--*/

/*--PORTFOLIO-SECTION-2  START-*/
.foodko-portfolio-2-box{
  text-align: center;
  padding-bottom: 24px;
  .foodko-portfolio-2-img{
    position: relative;
    img{
      width: 100%;
      height: 100%;
    }
  }
  .foodko-portfolio-2-content{
    background: var(--background-color);
    border-radius: 0 0 10px 10px;
    padding: 30px;
    h4{
      font-family: $heading-font;
    }
    p{
      margin-top: 15px;
      margin-bottom: 0;
    }
  }
}
/*--PORTFOLIO-SECTION-2  END--*/

/*--PORTFOLIO-SECTION-3 START--*/
.foodko-portfolio-3-all {
  display: flex;
  gap: 24px;
  @include max (1199px){
display: ruby-text;
  }
  .foodko-portfolio-3-1 {
    display: flex;
    gap: 24px;
    @include max (1199px){
      justify-content: space-around;
        }
        @include max (767px){
          display: contents;
            }
}
}
/*--PORTFOLIO-SECTION-3 END--*/