
.foodko-burger-price{
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding-bottom: 30px;
    position: sticky;
    top: 155px;
    &:last-child{
        padding-bottom: 0;
    }
    @include max(991px) {
        display: flow;
        top: 0;
      }
    .foodko-price-content{
        background: var(--white-color);
            padding: 40px;
            text-align: center;
            border-radius: 10px 0 0 10px;
            @include max(767px) {
                padding: 15px;
              }
            .foodko-price-content-box{
                padding: 40px 100px;
                border: 1px solid rgba(57, 61, 61, 0.3);
                border-radius: 10px;

                @include max(1199px) {
                    padding: 40px 50px;
                  }
                @include max(1199px) {
                    padding: 20px 30px;
                  }
                .foodko-price-content-text{
                    span{
                        font-weight: 700;
                        font-size: 50px;
                     line-height: 55px;
                     color: var(--accent-color);
                     @include max(767px) {
                        font-size: 36px;
                        line-height: 45px;
                      }
                    }
                    h3{
                       margin: 20px 0px 15px;
                       font-family: $heading-font;
                       @include max(767px) {
                        margin: 10px 0px 10px;
                      }
                    }
                    p{
                        margin: 0;
                    }
                }
                .foodko-content-price{
                    h4{
                        margin: 20px 0px 30px;
                        color: var(--accent-color);
                        span{
                            color: var(--body-color);
                            text-decoration: line-through;
                            padding-left: 10px;
                        }
                    }
                }
                .foodko-pice-content-button{
                   display: inline-flex;
                }
            }
           
        }
      

}


