.foodko-accordion-column {
    .foodko-accordion-wrap {
        .foodko-accordion-item {
            border-radius: 10px;
            padding: 25px;
            position: relative;
            margin-bottom: 24px;
            transition: all .4s;
           border: 1px solid rgba(57, 61, 61, 0.2);
            &:last-child{
                margin-bottom: 0px;
            }
            .foodko-accordion-header {
                cursor: pointer;
                position: relative;
                padding-right: 33px;
                .foodko-accordion-arrow {
                    position: absolute;
                    background-color: #EB0029;
                    border-radius: 50px;
                    width: 30px;
                    height: 30px;
                    text-align: center;
                    align-items: center;
                    justify-content: center;
                    display: flex;
                    font-weight: 700;
                    right: 0px;
                    transition: all .4s;
                    top: -2px;
                    font-size: 23px;
                    z-index: 2;
                }
        
                
                h4 {
                    font-family: $heading-font;
                    font-weight: 700;
                }
            }
            
            .foodko-accordion-body {
                display: none;
                padding-top: 15px;
                
                p {
                   margin-bottom: 0;
                }
            }
            
            &.open {
                border: none;
                background-color: var(--background-color);
                .foodko-accordion-body {
                    display: block;
                }
                .foodko-accordion-arrow {
                    transform: rotate(180deg);
                }
            }
        }
    }
}



