
/*---TEAM-SECTION all start --*/
.foodko-team-box{
    &.team-inner{
        padding-right: 53px;
    }
    @include max(1199px) {
        max-width: 416px;
        margin: 0 auto;
        margin-top: 24px;
      }
    &.about-inner{
        padding: 24px 0px;
        @include max(1199px) {
        padding: 0px;
          }
    }
    .foodko-team-content{
        background-color: var(--background-color);
          align-items: center;
          text-align: center;
          padding: 30px;
          border-radius: 10px;
          &.team-bg{
            background-color: var(--white-color);
          }
          .foodko-team-img{
            width: 100%;
            height: 100%;
          }
          .foodko-team-icon{
              text-align: center;
              padding-top: 24px;
              .foodko-social-icon{
    
              }
              .foodko-team-text{
                  h4{
                      padding: 15px 0px 6px;
                      font-family: $heading-font;
                  }
                  p{
                      margin: 0;
                  }
              }
          }
      }
}

/*---TEAM-SECTION all END --*/

.foodko-team-section{
    background-color: var(--background-color);
    position: relative;
    &.inner-bg{
        background-color: var(--white-color);
        .foodko-team-box{
            .foodko-team-content{
                background-color: var(--background-color);
            }
        }
       
    }
}
/*---TEAM-SECTION END --*/




/*---TEAM-SECTION INNER START --*/
.foodko-team-details{
    @include max(767px) {
        display: grid;
          }
ul{
    &.foodko-inner-title{
        display: flex;
        font-size: 24px;
        font-weight: 700;
        font-family:$heading-font;
        background: rgba(57, 61, 61, 0.2);
        padding: 20px 0px;
        color: #0A0F0F;
        @include max(767px) {
            display: grid;
            gap: 20px;
            padding: 20px;
              }
        li{
            width: 100%;
            align-items: center;
            text-align: center;
            @include max(767px) {
              text-align: start;
                  }
        }
    }
}
ul{
    &.foodko-inner-text{
        display: flex;
        font-size: 21px;
        padding: 20px 0px;
        background: var(--background-color);
        @include max(767px) {
            display: grid;
            gap: 20px;
            padding: 20px;
              }
        li{
            width: 100%;
            align-items: center;
            text-align: center;
            @include max(767px) {
                text-align: start;
                    }
            a{
                color: var( --heading-color);
            }
        }
    } 
}
}


/*---progress bar--*/
.foodko-team-progress-box{
    h3{
      padding-bottom: 20px;
      font-family: $heading-font;
      @include max(1199px) {
        text-align: center;
          }
    }
    .foodko-team-progress-img{
        display: flex;
        gap: 24px;
        padding-bottom: 24px;
   
        img{
            @include max(1199px) {
                width: 49%;
                  }
        }
    }
}



.foodko-extra-skilbar{
    margin-top: 40px;
    .skillbar{
        position: relative;
    display: inline-block;
    margin-bottom: 30px;
    width: 100%;
    width: 100%;
        p{
            &.skillbar-lable{
                margin-bottom: 20px;
                font-weight: 700;
                justify-content: space-between;
                display: flex;
            
                span{
                    &.skill-bar-title{

                    }
                    &.skill-bar-percent{
                        font-weight: 700;
                    }
                }
            }
        }
        .skillbar-box{
            height: 8px;
            border-radius: 5px;
            position: relative;
            background-color: rgba(13, 14, 29, .2);
            p{
                &.skillbar-bar{
                    height: 8px;
                    width: 0;
                    display: inline-block;
                    position: absolute;
                    left: 0;
                    margin-bottom: 0;
                    background-color: var(--accent-color);
                    border-radius: 5px;
                }
            }
        }
    }
}

/*---progress bar--*/


/*---TEAM-SECTION INNER END --*/