.foodko-cta-section{
    background-color: var(--background-color2);
    .foodko-hero-btn1{
        max-width: 646px;
        margin: 0 auto;
        position: relative;
        input{
            width: 100%;
            padding: 15px 40px 15px 49px;
            border: none;
            outline: none;
            border-radius: 3px;
            &::placeholder {
                color: var(--heading-colo); 
            }
            &.cta-padding{
                padding: 15px 145px 15px 27px;
                @include max (767px){
                    padding: 15px 27px 15px 27px;
                    }
            }
        }
        button#foodko-form-btn {
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            height: auto;
            right: 5px;
            @include max (767px){
                width: 100%;
                position: static;
                top: 50%;
                transform: translateY(-0%);
                height: auto;
                right: 5px;
                margin-top: 20px;
                }
        }
    }
}