.foodko-testimonial-section{
    .testimonial-align{
        align-items: center;
        position: relative;
        .foodko-testimonial-img{
          
            position: relative;
            a {
                display: block;
                position: relative;
                overflow: hidden;
                border-radius: 10px;
      
                img {
                  width: 100%;
                  height: auto;
                  transition: transform 0.3s ease;
                }
      
      
              }
              &:hover {
                img {
                  transform: scale(1.1); 
                }
      
                a:before {
                  opacity: 1;
                }
              }
            .foodko-testimonial-shape{

            }
        }
        .foodko-testimonial-content{
            @include max (1199px){
                padding-top: 24px;
              }
            p{
                margin-bottom: 60px;
                @include max (1399px){
                    margin-bottom: 40px;
                  }
            }
        }
       .foodko-testimonial-full-box {
        display: flex;
        gap: 24px;
        @include max (767px){
            display: grid;
          }
    
        .foodko-testimonial-box{
            padding: 30px;
            background: var( --background-color);
            border-radius: 10px;
            margin-right: 24px;
        
                .foodko-testimonial-rating{
                        display: inline-flex;
                        gap: 3px;

                }
                p{
                    margin: 15px 0px 25px;
                    font-size: 18px;
                }
                .foodko-testimonial-author-box{
                    display: flex;
                    gap: 10px;
                    align-items: center;
                    .foodko-testimonial-author-img{

                    }
                    .foodko-testimonial-author-text{
                        a{
                            color: var(--black-color1);
                            p{
                                font-family: $heading-font;
                                margin: 0px;
                                padding-bottom: 3px;
                                font-weight: 700;
                                font-size: 20px;
                            }
                        }
                       
                        h6{
                            font-size: 14px;
                            font-weight: 400;
                        }
                    }
                }
            }
        }
    }
}
.testimonial-slider-1 .slick-slide {
    margin-right: 24px;
    @include max (767px){
        margin-right: 2px;
      }
}



/*---testimonial-section-2 slider start-*/
/* Default slide */
.center-slider .slick-slide{
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease-in-out;
  }
  .center-slider .slick-current.slick-active{
    box-shadow: 0 4px 60px rgba(0, 0, 0, 0.1);
    background: var(--background-color);
    border-radius: 10px;
    margin-bottom: 30px;
  }
  .center-slider .slick-track {
    padding-top: 45px;
}
  .center-slider ul.slick-dots {
    position: absolute;
    bottom: -50px;
    display: flex;
    gap: 20px;
    justify-content: end;
    width: 55%;
    @include max (767px){
        width: 62%;
    }
}
.center-slider .slick-dots li button {
    font-size: 0;
    line-height: 0;
    display: block;
    width: 20px;
    height: 20px;
    padding: 5px;
    cursor: pointer;
    color: transparent;
    border: 0;
    outline: none;
    border-radius: 50px;
    background-color: var(--body-color);
}

.center-slider .slick-dots li button:hover {
    background-color:  var( --accent-color);
}
.center-slider .slick-dots li.slick-active button {
    background-color:  var( --accent-color);
  }
.center-slider .slick-dots li button:focus,
.center-slider .slick-dots li button.active {
    background-color: var( --accent-color);
}





  .foodko-testimonial-slider {
    display: flex;
    text-align: center;
    .foodko-testimonial-wrap{
        padding: 30px;
        margin-top: 61px;
        .foodko-testimonial-slider-icon{
            margin-top: -135px;
            justify-content: center;
            display: flex;
            img{
            
            }
        }
           
        p{
            padding: 20px 0px;
            margin: 0;
        }
        h5{
    
        }
        & h5.text-color{
            font-weight: 400;
            padding-top: 3px;
        }
    }

}
/*---testimonial-section-2 slider end-*/

/*---testimonial-section-3  start-*/
.foodko-testimonial-3-box {
    display: flex !important;
    background: var(--background-color);
    padding: 54px;
    border-radius: 10px;
    align-items: center;
    margin-right: 20px !important;
    @include max (991px){
        display: flow !important;
        text-align: center;
       }
       @include max (767px){
        padding: 20px;
       
       }
       &.testimonial-inner{
        padding: 27px 30px 27px 26px;
        border-radius: 10px;
        align-items: center;
        margin-right: 0px !important;
        margin-top: 24px;
        // &:last-child{
        //     margin-top: 0px;
        // }
        @include max (767px){
            padding: 20px 20px 20px 20px;
           
           }
    
        .foodko-testimonial-3-img-box{
            flex-shrink: 0;
          
        }
        .foodko-testimonial-3-content{
            padding-left: 12px;
            h5{
                margin: 20px 0;
                font-weight: 400;
                @include max (767px){
                    margin: 10px 0;
                   
                   }
            }
        }
       }
    .foodko-testimonial-3-img-box{
       flex-shrink: 0;
       @include max (991px){
        display: flex;
        justify-content: center;
        padding-bottom: 20px;
    
       }
        img{
            object-fit: cover;
        }
    
    }
    .foodko-testimonial-3-content{
        padding-left: 40px;
        @include max (991px){
            padding-left: 0;
           }
        .foodko-testimonial-3-rating{
            display: inline-flex;
            gap: 3px;
        }
        p{
            margin: 25px 0px;
            @include max (991px){
                margin: 10px 10px;
               }
        }
        .foodko-testimonial-3-author-box{
            .foodko-testimonial-3-author-text{
                a{
                    p{
                        margin: 0;
                        font-weight: 700;
                        color: var(--heading-color);
                        padding-bottom: 3px;
                    }
                }
                h6{
                font-size: 18px;
                color: var( --body-color);
                font-weight: 400;
                }
            }
        }
    }
}

.foodko-slider-4 .slick-slide {
    margin-right: 24px;
}
/*---testimonial-section-3  start-*/