.contact-inner{
    gap: 36px;
    display: flex;
    @include max(767px) {
        display: contents;
      }
    .foodko-site-title-box{
        padding-right: 0;
        @include max(767px) {
            padding-bottom: 0px;
          }
        .foodko-icon-box {
            display: flex;
            gap: 15px;
            padding-bottom: 50px;
            @include max(767px) {
                padding-bottom: 20px !important;
              }
            &:last-child{
                padding-bottom: 0px;
            }
            p{
                margin-bottom: 8px;
            }
            a{
            font-size: 18px;
            font-weight: 700;
            color: var(--heading-color);
            }
            
        }
    }
   
}