.foodko-breadcrumb{
  text-align: center;
  padding: 268px 0 130px;
  background-color: var(--background-color);
  @include max(991px) {
    padding: 170px 0 100px;
  }
  @include max(767px) {
    padding: 170px 0 80px;
  }
  @include max(575px) {
    padding: 130px 0 80px;
  }
}

h1.post__title{
    margin-bottom: 50px;
    line-height: 110px;
    font-size: 100px;
    font-family: $heading-font;
  @include max(1400px) {
    margin-bottom: 30px;
  }
  @include max(1199px) {
   font-size: 90;
   line-height: 100px;
  }
  @include max(767px) {
    font-size: 50px;
    line-height: 60px;
    margin-bottom: 15px;
   }
}

.breadcrumbs{
  ul{
    li{
      font-size: 21px;
      font-weight: 700;
      line-height: 1;
      z-index: 0;
      padding: 0px 15px 0px 0px;
      position: relative;
      display: inline-block;
      color: var(--accent-color);
      @include max(767px) {
        font-size: 18px;
      }
      &:first-child{
        padding-left: 0;
      }
      &:last-child{
        padding-right: 0;
      }
      &:before {
        content: "";
        right: -8px;
        top: 0;
        width: 100%;
        height: 100%;
        z-index: -1;
        position: absolute;
        background-size: 9px;
        background-position: center right;
        background-repeat: no-repeat;
        background-image: url(../images/icon/arrow-right7.svg);
      }
      &:last-child:before{
        content: none;
      }
      a{
        font-weight: 400;
        color: var(--heading-color);
        &:hover{
          color: var(--accent-color);
          font-weight: 700;
        }
      }
    }
  }
}
