.foodko-social-icon{
  &.inner-blog-details{
    h3{
      font-family: $heading-font;
      margin-right: 14px;
    }
  }
  h4{
    margin: 50px 0px 15px;
    font-family: $heading-font;
    @include max (1199px){
     text-align: center;
   }
   @include max (991px){
    margin: 30px 0px 10px;
  }
  }
    ul {
        display: flex;
        justify-content: end;
        margin: 0;
        padding: 0;
        gap: 10px;
        align-items: center;
        &.center{
            justify-content: center;
               li {
          list-style: none;
          width: 35px;
          height: 35px;
          border: 1px solid var(--black-color);
          align-items: center;
          text-align: center;
          display: grid;
          border-radius: 50%;
          // &.color{
          //   border: 2px solid var(--black-color);
          // }
  
          &:hover {
            background-color: var(--accent-color);
            border: 2px solid var(--accent-color);
            transition: 0.3s;
  
            a {
              path {
                stroke:  var(--white-color);
              }
            }
          }
          a {
            path {
              fill: var(--black-color);
            }
          }
        }
        &.start{
          justify-content: start;
      }
      &.social-inner{
        justify-content: start;
        @include max (1199px){
          justify-content: center;
       }
      }
        }
        li {
          list-style: none;
          width: 35px;
          height: 35px;
          border: 1px solid var(--accent-color);
          align-items: center;
          text-align: center;
          display: grid;
          border-radius: 50%;
          // &.color{
          //   border: 2px solid var(--black-color);
          // }
  
          &:hover {
            background-color: var(--accent-color);
            color: var(--white-color2);
            transition: 0.3s;
  
            a {
              path {
                fill: var(--white-color);
              }
            }
          }
  
          &.clicked {
            background-color: var(--accent-color);
            color: var(--white-color2);
  
            a {
              path {
                fill: var(--white-color);
              }
            }
          }
  
          a {
            path {
              fill: var(--accent-color);
            }
          }
        }
      }
}