/*form-SECTION START*/

.foodko-form-section{
  background: var(--white-color);
  padding: 50px;
  border-radius: 10px;
  display: grid;
  gap: 24px;
  margin-left: 26px;
  @include max (767px){
    padding: 24px;
    }
    &.blog-inner{
      padding: 0;
      margin: 0;
      .contact-page__input-box input{
        padding: 15px 17px 15px 17px;
      }
    }
    &.reservation-inner{
      background-color: var(--background-color);
    }
  &.two{
    background: none;
    padding: 0;
   .foodko-form-title{
   h4{
    color: var(--white-color);
    font-size: 40px;
    font-weight: 700;
   }
   }
   .contact-page__input-box{
    border: 1px solid var(--white-color2);
    

    input{
      background-color: transparent;
      color: var(--white-color);
      &::placeholder {
        color: var(--heading-colo); 
    }
    }
   }

  }
  &.three{
    background: var(--background-color);
    padding: 30px;
    margin-left: 0px;
    gap: 0;
    &.foodko-form-section .contact-page__input-box{
      margin-bottom: 24px;
    }
  }
  &.inner-form{
    background-color: var(--background-color);
    margin-left: 14px;
    padding: 30px;
   
  }
  @include max (1199px){
    margin-left: 10px;
    }
    @include max (991px){
      margin-left: 0px;
      margin-top: 24px;
      }
      .contact-page__input-box{
        position: relative;
        width: 100%;
        border: 1px solid  rgba(57, 61, 61, 0.2);
        border-radius: 3px;
      
        img{
          position: absolute;
          top: 50%;
          transform: translateY(-50%);
          height: auto;
          width: 24px;
          left: 17px;
        }
        input{
          width: 100%;
          background:  transparent;
          padding: 15px 40px 15px 49px;
          border: none;
          outline: none;
          
          &::placeholder {
            color: var(--heading-color); 
        }
        }
      }
  button#foodko-form-btn {
    width: 100%;
    padding: 10px;
    font-size: 18px;
    font-weight: 700;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .4s;
    border-radius: 3px;

    }

}
button#foodko-inner-blog-btn {
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .4s;
  border-radius: 3px;

  }

/*form-SECTION end*/



/*BOOKING-SECTION START inner*/
  .foodko-booking-align{
    align-items: center;

  }
 /*BOOKING-SECTION end inner*/

.foodko-booking-box{
  background: var(--white-color);
  padding: 50px;
  text-align: center;
  border-radius: 10px;
  @include max (1399px){
    padding: 25px;
    }
    &.reservation-inner{
      background-color: var(--background-color);
    }
  .foodko-booking-text{
    max-width: 290px;
    margin: 0 auto;
    text-align: center;
    h3{
      padding-bottom: 25px;
      border-bottom: 1px solid var(--accent-color);
      margin-bottom: 30px;
      font-family: $heading-font;
    }
  }
  ul{
    li{
      justify-content: space-between;
      display: flex;
      margin-bottom: 15px;
      border-bottom: dotted rgba(57, 61, 61, 0.2);
      padding-bottom: 15px;
      font-weight: 700;
      &:last-of-type{
        border-bottom: none;
        margin-bottom: 0;
        padding-bottom: 0px;
      }
      span{

      }
    }
  }
  .foodko-booking-contact{
    max-width: 250px;
    margin: 0 auto;
    text-align: center;
    padding-top: 30px;
    h3{
      padding-bottom: 20px;
      border-bottom: 1px solid var(--accent-color);
      margin-bottom: 20px;
      font-family: $heading-font;
    }
    a{
    color: var(--accent-color);
    font-weight: 700;
    }
  }
}
/*BOOKING-SECTION END*/

/*CONTACT-SECTION START*/

.foodko-contact-section{
  background-color: var( --heading-color);
 
  .foodko-contact-content{
    @include max (1199px){
     max-width: 610px;
       }
      .foodko-contact-text{
          h2{
              color: var(--white-color);
              font-family: $heading-font;
          }
          p{
              color: var(--white-color2);
              margin: 18px 0px 50px;
              @include max (1199px){
               margin: 15px 0px 30px;
                }
          }
      }
      .foodko-contact-call{
          display: flex;
          align-items: center;
          gap: 15px;
          @include max (1199px){
          margin-bottom: 40px;
          }
          .foodko-contact-icon{

          }
          .foodko-cotact-text{
              p{
                  font-size: 20px;
                  margin-bottom: 8px;
                  color: var(--white-color2);
              }
              h3{
                  color: var(--white-color);
              }
          }
      }
  }
}

/*CONTACT-SECTION END*/



















.foodko-subscription2{
  @include max(1199px) {
   padding-top: 24px;
   padding-bottom: 40px;
    }
    // @include max(767px) {
    //   padding-top: 24px;
    //   max-width: 335px;
    //   margin: 0 auto;
    //    }
  &.one{
      max-width: 508px;
  }
  h3{
      padding-bottom: 30px;
      @include max(767px) {
        padding-bottom: 10px;
         }
      }
      form{
          position: relative;
          width: 516px;
          @include max(767px) {
              width: 334px;
            }
          &.max{
              width: 100%;
          }
          input{
              height: 60px;
              font-size: 18px;
              font-weight: 700;
             padding: 15px 142px 15px 17px;
              border: 1px solid #eaedf0 !important;
              width: 100%;
              border-radius: 3px;
              @include max(767px) {
                padding: 15px 24px 15px 17px;
               }
             &:focus{
                border: 1px solid #eaedf0!important;
              }
              @include max(767px) {
                width: 100%;
                margin-bottom: 20px;
              }
          }
          .foodko-footer-btn{
              position: absolute;
              right: 5px;
              top: 5px;
              @include max(767px) {
               position: static;
              }
          }
          
      }
}